<template>
  <div>
    <div class="stat__header">
      <div class="fl">会员统计
        <el-tooltip placement="right" effect="light">
          <div slot="content">累计会员总数： 累计注册用户的总数，相同会员多次记一个
            <br>新增会员数：新增注册用户数，相同会员多次记一个
            <br>充值会员数：充值金额的会员数，相同会员多次记一个
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <el-row :gutter="20" class="stat-card__list">
          <el-col :span="6">
            <li class="stat-card">
              <p>累积会员总数</p>
              <count-to :endVal="newStatistics.sumMemberCount" class="count-val"></count-to>
            </li>
          </el-col>
          <el-col :span="6">
            <li class="stat-card">
              <p>新增会员总数</p>
              <count-to :endVal="newStatistics.sumNewMember" class="count-val"></count-to>
            </li>
          </el-col>
          <el-col :span="6">
            <li class="stat-card">
              <p>充值会员数</p>
              <count-to :endVal="newStatistics.sumRechargeMember" class="count-val"></count-to>
            </li>
          </el-col>
        </el-row>
        <div id="countChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { getMemberStatistics } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'countChart',
      optionsData: {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['累积会员总数', '新增会员总数', '充值会员数'],
          left: 30,
          top: 20
        },
        grid: {
          top: 60,
          left: 30,
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true,
          splitLine: {
            show: false
          },
          data: ['00-00', '0-0', '0-0']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value'
          },
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                type: 'dotted'
              }
            },
            type: 'value',
            axisLabel: {
              formatter: value => {
                var texts = []
                const obj = value * 100
                texts.push(obj + '%')
                return texts
              }
            }
          }
        ],
        series: [
          {
            name: '累积会员总数',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '新增会员总数',
            type: 'line',
            smooth: true,
            data: []
          },
          {
            name: '充值会员数',
            type: 'line',
            smooth: true,
            data: []
          }
        ]
      },
      newStatistics: {
        sumMemberCount: 0,
        sumNewMember: 0,
        sumRechargeMember: 0
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      getMemberStatistics(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.newStatistics = res.data.newStatistics
            this.optionsData.xAxis.data = res.data.statisticsDate
            this.optionsData.series[0].data = res.data.countMember
            this.optionsData.series[1].data = res.data.newMember
            this.optionsData.series[2].data = res.data.rechargeMember
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



