<template>
  <div>
    <div class="stat__header">
      <div class="fl">成交会员分析
        <el-tooltip placement="right" effect="light">
          <div slot="content">
            新成交会员：统计时间外无成交记录的会员
            <br>老成交会员：统计时间外已存在成交记录的会员
            <br>会员数： 会员类型的总数
            <br>会员占比：会员类型数占总成交会员比例
            <br>客单价：会员类型成交总金额 / 会员类型成交总人数
            <br>付款金额： 会员类型成交总金额
            <br>
            访问-付款转化率：成交会员总数 / 总访客人数
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <el-radio-group v-model="value" @change="changeChartData" size="mini">
          <el-radio-button label="会员数"></el-radio-button>
          <!-- <el-radio-button label="会员数占比"></el-radio-button> -->
          <el-radio-button label="客单价"></el-radio-button>
          <el-radio-button label="付款金额"></el-radio-button>
          <el-radio-button label="访问-付款转化率"></el-radio-button>
        </el-radio-group>
        <table class="stat-inner__table mt-20">
          <tr>
            <th>会员类型</th>
            <th>会员数</th>
            <th>会员数占比</th>
            <th>客单价</th>
            <th>付款金额</th>
            <th>访问-付款转化率</th>
          </tr>
          <tr>
            <td>新增成交会员</td>
            <td>{{newStatistics.newDealMember}}</td>
            <td>{{newStatistics.newDealProportion}}%</td>
            <td>¥ {{$cms.float(newStatistics.newDealAverage)}}</td>
            <td>¥ {{$cms.float(newStatistics.newDealPaymentAmount)}}</td>
            <td>{{newStatistics.newDealConversionRate}}%</td>
          </tr>
          <tr>
            <td>旧成交会员数</td>
            <td>{{newStatistics.usedDealMember}}</td>
            <td>{{newStatistics.usedDealProportion}}%</td>
            <td>¥ {{$cms.float(newStatistics.usedDeaPverage)}}</td>
            <td>¥ {{$cms.float(newStatistics.usedDealPaymentAmount)}}</td>
            <td>{{newStatistics.usedDealConversionRate}}%</td>
          </tr>
        </table>
        <div id="dealChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { getDealMemberAnalysis } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'dealChart',
      optionsData: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: 'left',
          bottom: 0,
          top: 30,
          right: 0,
          containLabel: true
        },
        xAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: []
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'value'
        },
        series: {
          name: '访问会员数',
          type: 'bar',
          barMaxWidth: 25,
          barCategoryGap: '10%',
          data: [320, 302, 301, 334, 390, 330, 320]
        }
      },
      newStatistics: {
        usedDealProportion: 0,
        newDealConversionRate: 0,
        usedDealMember: 0,
        usedDeaPverage: 0,
        usedDealConversionRate: 0,
        newDealPaymentAmount: 0,
        usedDealPaymentAmount: 0,
        newDealMember: 0,
        newDealProportion: 0,
        newDealAverage: 0
      },
      localData: {},
      value: '会员数'
    }
  },
  methods: {
    changeChartData(value) {
      if (value === '会员数' || value === '付款金额') {
        // 柱状图
        this.optionsData.series = {
          name: value,
          type: 'bar',
          barMaxWidth: 25,
          barCategoryGap: '10%'
        }
        if (value === '会员数') {
          this.optionsData.series.data = this.localData.dealMember
        }
        if (value === '付款金额') {
          this.optionsData.series.data = this.localData.dealMemberPay
        }
        this.chart.setOption(this.optionsData)
      } else {
        // 折线图
        this.optionsData.series = {
          name: value,
          type: 'line'
        }
        // if (value === '会员数占比') {
        //   this.optionsData.series.data = this.localData.dealMember
        // }
        if (value === '客单价') {
          this.optionsData.series.data = this.localData.dealMemberAverage
        }
        if (value === '访问-付款转化率') {
          this.optionsData.series.data = this.localData.dealConversionRate
        }
        this.chart.setOption(this.optionsData)
      }
    },
    getData() {
      this.loading = true
      getDealMemberAnalysis(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.localData = res.data
            this.optionsData.xAxis.data = res.data.statisticsDate
            this.optionsData.series.data = res.data.dealMember // 默认会员
            this.newStatistics = res.data.newStatistics
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



