<template>
  <div>
    <div class="stat__header">
      <div class="fl">活跃会员
        <el-tooltip placement="right" effect="light">
          <div slot="content">访问会员数： 访问系统的会员数，相同会员多次记一个
            <br>加购会员数：成功加入购物车的会员数，相同会员多次记一个
            <br>成交会员数：成功下单且支付的会员总数，相同会员多次记一个
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">
        <el-row class="stat-card__list" :gutter="20">
          <el-col :span="6">
            <li class="stat-card">
              <p>访问会员数</p>
              <count-to :endVal="newStatistics.newVisitMember" class="count-val"></count-to>
              <p class="card-prop">
                <span>占比:</span>
                <span class="up">{{newStatistics.visitProportion}}%</span>
              </p>
            </li>
          </el-col>
          <el-col :span="6">
            <li class="stat-card">
              <p>加购会员数</p>
              <count-to :endVal="newStatistics.newAddPurchaseMember" class="count-val"></count-to>
              <p class="card-prop">
                <span>占比:</span>
                <span class="up">{{newStatistics.addPurchaseProportion}}%</span>
              </p>
            </li>
          </el-col>
          <el-col :span="6">
            <li class="stat-card">
              <p>成交会员数</p>
              <count-to :endVal="newStatistics.newDealMember" class="count-val"></count-to>
              <p class="card-prop">
                <span>占比:</span>
                <span class="up">{{newStatistics.dealProportion}}%</span>
              </p>
            </li>
          </el-col>
        </el-row>
        <div id="activeChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { getActiveMember } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'activeChart',
      optionsData: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['访问会员数', '加购会员数', '成交会员数'],
          left: 'left'
        },
        grid: {
          left: 'left',
          bottom: 0,
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
        },
        series: [
          {
            name: '访问会员数',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight',
                formatter: function(data) {
                  if (data.value === 0) {
                    return ''
                  } else {
                    return data.value
                  }
                }
              }
            },
            data: [320, 302, 301, 334, 390, 330, 320]
          },
          {
            name: '加购会员数',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight',
                formatter: function(data) {
                  if (data.value === 0) {
                    return ''
                  } else {
                    return data.value
                  }
                }
              }
            },
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '成交会员数',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight',
                formatter: function(data) {
                  if (data.value === 0) {
                    return ''
                  } else {
                    return data.value
                  }
                }
              }
            },
            data: [220, 182, 191, 234, 290, 330, 310]
          }
        ]
      },
      newStatistics: {
        visitProportion: 0,
        newAddPurchaseMember: 0,
        addPurchaseProportion: 0,
        newDealMember: 0,
        dealProportion: 0,
        newVisitMember: 0
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      getActiveMember(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.yAxis.data = res.data.statisticsDate
            this.optionsData.series[0].data = res.data.visitMember
            this.optionsData.series[1].data = res.data.addPurchaseMember
            this.optionsData.series[2].data = res.data.dealMember
            this.newStatistics = res.data.newStatistics
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



