<template>
<div class="stat__layout">
 <timer @change="getTime"></timer>
 <count-member class="mt-20" :time-data="value"></count-member>
  <active-member class="mt-20" :time-data="value"></active-member>
  <deal-member class="mt-20" :time-data="value"></deal-member>
</div>
</template>

<script>
 
import dealMember from './dealMember'
import activeMember from './activeMember'
import countMember from './countMember'
import timer from './../components/time'
export default {
   components: {
     dealMember,
     activeMember,
     countMember,
     timer
   },
   data() {
     return {
       value: ['', '']
     }
   },
   methods: {
     getTime(time) {
       this.value = time
     }
   }
}
</script>

<style>
</style>



